import Input from "./Input";
import {Form, Formik} from "formik";
import * as Yup from 'yup';
import {useDispatch} from "react-redux";
import {actionClearCart} from "../../reducers/items.reducer";
import './CardForm.scss';
import PropTypes from "prop-types";
import Header from "../Header";

function CardForm({setRerender}) {
    const dispatch = useDispatch();
    const cart = JSON.parse(localStorage.getItem("cart"));
    const order = cart.map(item => item.name).toString();

    return (
        <Formik
            initialValues={{
                firstName: "",
                secondName: "",
                age: "",
                delivery: "",
                phone: "",
            }}
            validationSchema={Yup.object({
                firstName: Yup.string()
                    .min(2, "  '")
                    .max(10, "  '")
                    .required(" '  "),
                secondName: Yup.string()
                    .min(5, "  ")
                    .max(12, "  ")
                    .required(" '  "),
                age: Yup.number()
                    .min(18, "  18 ")
                    .max(99, "  ")
                    .required(" '  "),
                delivery: Yup.string()
                    .min(10, "  ")
                    .max(30, "  ")
                    .required(" '  "),
                phone:  Yup.number()
                    .required(" '  "),
            })}
            onSubmit={(values) => {
                console.log(values,order)
                dispatch(actionClearCart());
                setRerender();
            }}
        >
            <Form className="cart-form">
                <fieldset className="form-block">
                    <legend> </legend>
                    <Input label="'" placeholder="'" type="text" name="firstName"/>
                    <Input label="" placeholder="" type="text" name="secondName"/>
                    <Input label="" placeholder="" type="number" name="age"/>
                    <Input label=" " placeholder=" " type="text" name="delivery"/>
                    <Input label="" placeholder="" type="number" name="phone"/>
                    <button type="submit" className="cart-button"></button>
                </fieldset>

            </Form>
        </Formik>
    )
}

Header.propTypes = {
    setRerender: PropTypes.func,
}

export default CardForm;
